import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getDisciplineBehaviors, deleteDisciplineBehavior } from 'services/DisciplineBehavior';

class ListTypeDisciplineBehaviorsPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteDisciplineBehavior(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên hành vi',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#typeDisciplineBehaviors/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Hành vi kỷ luật">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/typeDisciplineBehaviors/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getDisciplineBehaviors}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeDisciplineBehaviorsPage />
  </Page>
);



